import React, { FunctionComponent } from 'react'
import { View, Text, StyleSheet, Pressable } from 'react-native'
import { createBottomTabNavigator } from '@react-navigation/bottom-tabs'

import { TopTabNavigator } from './TopTabNavigator'

import NotFoundScreen from '../screens/NotFoundScreen'
import SettingsScreen from '../screens/SettingsScreen'
import HelpScreen from '../screens/HelpScreen'
import DetailsScreen from '../screens/DetailsScreen'
import SearchScreen from '../screens/SearchScreen'
import FavoritesScreen from '../screens/FavoritesScreen'

import { BottomNavigationBar } from '../components/BottomNavigationBar'
import { DropdownList } from '../components/DropdownList'
import { NavbarIconButton } from '../components/NavbarIconButton'

import { RootStackParamList } from '../types'
import Colors from '../constants/Colors'
import { ClientProvider } from '../api'
import {
  ContentTagsProvider,
  SentimentTagsProvider,
  SourceTagsProvider,
  UserProvider,
} from '../context'
import ListScreen from '../screens/ListScreen'

const titles: Record<string, string> = {
  Root: '',
  Favorites: 'Kedvencek',
  Search: 'Keresés',
  Settings: 'Beállítások',
  Help: 'Segítség',
  Details: 'Cikk',
  NotFound: 'Oops!',
}

const ROBOTO_500MEDIUM = 'Roboto_500Medium'

export interface IRootNavigatorProps {}
const BottomTab = createBottomTabNavigator<RootStackParamList>()

const navbarIcons = {
  SETTINGS: {
    name: 'settings',
    direction: 'Settings',
  },
  HELP: {
    name: 'help-outline',
    direction: 'Help',
  },
  BACK: {
    name: 'keyboard-arrow-left',
    direction: 'Root',
  },
}

const styles = StyleSheet.create({
  screenOptionsHeader: {
    height: 50,
    backgroundColor: Colors.oxfordBlue,
    justifyContent: 'space-between',
    alignItems: 'center',
    flexDirection: 'row',
  },
  screenOptionsHeaderTitle: {
    color: Colors.orangeWeb,
    fontFamily: ROBOTO_500MEDIUM,
    fontSize: 18,
  },
  headerSide: {
    minWidth: 80,
  },
})

export const RootNavigator: FunctionComponent<IRootNavigatorProps> = () => {
  return (
    <ClientProvider useMocks>
      <UserProvider>
        <SourceTagsProvider>
          <SentimentTagsProvider>
            <ContentTagsProvider>
              <BottomTab.Navigator
                initialRouteName="Root"
                tabBar={(props) => <BottomNavigationBar {...props} />}
                screenOptions={{
                  header: ({ navigation, route, options }) => (
                    <View style={styles.screenOptionsHeader}>
                      <View style={styles.headerSide}>
                        {route.name === 'Root' ||
                        route.name === 'Favorites' ||
                        route.name === 'Search' ? (
                          <NavbarIconButton
                            navigation={navigation}
                            icons={[navbarIcons.SETTINGS, navbarIcons.HELP]}
                          />
                        ) : (
                          <NavbarIconButton
                            navigation={navigation}
                            icons={[navbarIcons.BACK]}
                          />
                        )}
                      </View>
                      <View>
                        <Text style={styles.screenOptionsHeaderTitle}>
                          {titles[route.name]}
                        </Text>
                      </View>
                      <View style={styles.headerSide}>
                        {route.name === 'Root' && <DropdownList />}
                      </View>
                    </View>
                  ),
                }}
              >
                <BottomTab.Screen
                  name="Root"
                  component={TopTabNavigator}
                  options={{ unmountOnBlur: true }}
                />
                <BottomTab.Screen name="Search" component={ListScreen} />
                <BottomTab.Screen name="Favorites" component={ListScreen} />
                <BottomTab.Screen name="NotFound" component={NotFoundScreen} />
                <BottomTab.Screen name="Settings" component={SettingsScreen} />
                <BottomTab.Screen name="Help" component={HelpScreen} />
                <BottomTab.Screen name="Details" component={DetailsScreen} />
              </BottomTab.Navigator>
            </ContentTagsProvider>
          </SentimentTagsProvider>
        </SourceTagsProvider>
      </UserProvider>
    </ClientProvider>
  )
}
