import React, { FunctionComponent } from 'react'
import { createMaterialTopTabNavigator } from '@react-navigation/material-top-tabs'
import Colors from '../constants/Colors'
import ListScreen from '../screens/ListScreen'
import { RootTabParamList } from '../types'
import { UserConsumer } from '../context'
import { TopTabNavigationBar } from '../components/TopTabNavigationBar'

const TopTab = createMaterialTopTabNavigator<RootTabParamList>()

export const TopTabNavigator: FunctionComponent = ({ navigation }: any) => {
  return (
    <UserConsumer>
      {(user) => (
        <TopTab.Navigator
          initialRouteName="ArticleList"
          tabBar={(
            props: JSX.IntrinsicAttributes & {
              state: any
              descriptors: any
              navigation: any
              position: any
            }
          ) => <TopTabNavigationBar {...props} />}
          screenOptions={{
            tabBarActiveTintColor: Colors.oxfordBlue,
            tabBarScrollEnabled: true,
            tabBarStyle: { height: 50 },
          }}
        >
          <TopTab.Screen
            name="ArticleList"
            initialParams={{}}
            component={ListScreen}
            options={{
              title: 'Minden Téma',
            }}
          />
          {user?.preferredTags.map((tag) => (
            <TopTab.Screen
              key={tag.value}
              name={`${tag.value}List` as any}
              initialParams={{ contentTag: tag }}
              component={ListScreen}
              options={{
                title: tag.title,
              }}
            />
          ))}
        </TopTab.Navigator>
      )}
    </UserConsumer>
  )
}
