import React from 'react'
import { View, Text, StyleSheet } from 'react-native'
import Icon from 'react-native-vector-icons/MaterialIcons'
import Colors from '../constants/Colors'
import { UserConsumer } from '../context'
import JSON from '../package.json'

const styles = StyleSheet.create({
  containerView: {
    backgroundColor: Colors.white,
    height: window.screen.height - 50,
  },
  headerView: {
    height: 40,
    backgroundColor: Colors.platinum,
    alignItems: 'flex-start',
    justifyContent: 'center',
  },
  headerText: {
    color: Colors.oxfordBlue,
    fontFamily: 'Roboto_500Medium',
    fontSize: 17,
    marginHorizontal: 13,
  },
  view: {
    height: 50,
    alignItems: 'center',
    justifyContent: 'space-between',
    flexDirection: 'row',
  },
  viewSplit: {
    borderBottomWidth: 1,
    borderBottomColor: Colors.platinum,
  },
  text: {
    color: Colors.oxfordBlue,
    fontFamily: 'Roboto_400Regular',
    fontSize: 15,
    marginHorizontal: 13,
  },
  icon: {
    marginHorizontal: 13,
  },
  info: {
    marginHorizontal: 13,
    fontSize: 14,
    fontFamily: 'Roboto_300Light',
    color: Colors.oxfordBlue,
  },
})

export default function SettingsScreen() {
  return (
    <UserConsumer>
      {(user) => {
        const content = [
          {
            Title: 'Hírek',
            Content: [
              {
                Title: 'Kategóriák',
                Direction: 'CategoriesScreen',
                Info: null,
              },
            ],
          },
          {
            Title: 'Rólunk',
            Content: [
              { Title: 'Verzió ' + JSON.version, Direction: null, Info: null },
              { Title: 'E-mail', Direction: null, Info: user?.email },
            ],
          },
        ]
        return (
          <View style={styles.containerView}>
            {content.map((data) => {
              return (
                <>
                  <View style={styles.headerView}>
                    <Text style={styles.headerText}>{data.Title}</Text>
                  </View>
                  {data.Content.map((subData, index) => {
                    return (
                      <View
                        style={[
                          styles.view,
                          index + 1 < data.Content.length && styles.viewSplit,
                        ]}
                      >
                        <Text style={styles.text}>{subData.Title}</Text>
                        {subData.Direction && (
                          <Icon
                            name="keyboard-arrow-right"
                            size={24}
                            color={Colors.oxfordBlue}
                            style={styles.icon}
                          />
                        )}
                        {subData.Info && (
                          <Text style={styles.info}>{subData.Info}</Text>
                        )}
                      </View>
                    )
                  })}
                </>
              )
            })}
          </View>
        )
      }}
    </UserConsumer>
  )
}
